@import './../../../styles/abstracts/functions';
@import './../../../styles/abstracts/mixins';
@import './../../../styles/abstracts/variables';

.todoslist {
	background-color: map-deep-get(
		$colors,
		'dark-theme',
		'clr-very-dark-desaturated-blue'
	);
	&--light {
		background-color: map-deep-get(
			$colors,
			'light-theme',
			'clr-very-light-grayish-blue'
		);
	}
}
